.login-btn {
  display: block;
  text-align: right;
  border-style: solid;
  border-width: 3px;
  box-shadow: 5px 5px 0px;
  padding: 5px 5px 5px 40px;
  margin-right: 10px;
  font-size: 1.1em;
  float: right;
  letter-spacing: 3px;
}

.login-btn:hover{
  background-color: #dd9933;
  box-shadow: 15px 15px 0px;
  transition: 1s cubic-bezier(0.075, 0.82, 0.165, 1);
}

