.main-text-container {
  /* background-color: aqua; */
  display: inline-flex;
  width: 30%;
  height: 90vh;
  flex-direction: row;
  text-align: right;
  padding-right: 10px;
  justify-content: right;
  align-content: center;
  flex-wrap: wrap;
}

.main-header {
  line-height: 10px;
}

.author-name {
  font-size: 4.5vw;
}

.author-blurb {
  font-size: 1vw;
  font-weight: 450;
  letter-spacing: 2px;
  line-height: 1vw;
}

.nav {
  align-self: flex-end;
  padding-bottom: 10%;
  position: absolute;
}

.render-container {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.head-render {
  height: 300px;
  /* background-color: red; */
}

.scroll-info {
  position: fixed;
  left: 50%;
  transform: translate(-50%, 0);
  text-decoration: line-through;
}

li {
  list-style-type: none;
  text-decoration: line-through;
}

.nav-item:hover{
  /* background-color: red!important; */
  text-decoration: underline;
  animation: bounce .1s ease;
  cursor: pointer;
}

@keyframes bounce {
  0%   { transform: translateX(0px); }
  50%   { transform: translateX(-10px); }
  100% { transform: translateX(0px);}
}

